var graceChecker = require("../../graceUI/graceChecker.js");
import LongButton from '../../components/my-components/LongButton';
let requesting = false;
let canLogin = false; //可否登录 
export default {
	components: {
		LongButton
	},
	data() {
		return {
			openEye: false,
			userId: '',
			password: '',
			idError: false, //用户信息错误
		}
	},
	computed: {
		disableButton() {
			this.idError = false;
			return !this.userId || !this.password;
		}
	},
	onLoad(option) {
		this.initPage()
		if (!option.from) { //如果没有from就说明是刚进入小程序
		} else {
			console.log(this.Cacher.getData(option.from)); //获取页面传参
		}
	},
	mounted() {
		this.closePageLoading()
	},
	methods: {
		initPage() {
			this.openEye = false;
			canLogin = false;
			this.userId = 'yilianxinpin';
			this.password = 'Qm8xn4KVBMc0Wd70';
			this.idError = false;
		},
		getUserId(val) {
			this.userId = val.detail;
		},
		getPassWord(val) {
			this.password = val.detail;
		},
		clearInput(key) {
			this[key] = '';
		},
		clickPWIcon() { //切换加密显示与明文显示
			this.openEye = !this.openEye;
		},
		loginWithWx: function() {
			this.closePageLoading();
			this.Toast('当前微信暂未绑定任何管理员账号');
		},
		loginNow: function(e) { //点击登录
			if (!requesting) { //函数节流
				requesting = true; //是否正在请求接口
				this.pageLoading();
				this.Request('login', {
					account: this.userId,
					password: this.password
				}).then(res => {
					// 验证通过
					this.closePageLoading();
					if (res.error == 0) {
						canLogin = true;
						if (canLogin) {
							uni.reLaunch({
								url: '../../pagesLogin/pages/selectShop'
							})
						} else {
							this.idError = true;
						}
						requesting = false;
					} else {
						requesting = false;
						this.Toast(res.message)
					}
				})
			}
		},
		reg: function() { //找回密码
			uni.navigateTo({
				url: '../../pagesLogin/pages/index'
			})
		}
	}
}
